<template>
  <div class="content">
    <div class="row">
      <div class="col-3" v-for="(item, i) in htmls">
        <card
        type="chart">
          <h4 class="card-title">{{ item }}</h4>
          <a :href="'#/dashboard/'+i" class="btn btn-primary">Go</a>
        </card>
      </div>
    </div>
  </div>
</template>
<script>

import {
  Card
} from "@/components/index";

import LineChart from '@/components/Charts/LineChart';
import BarChart from '@/components/Charts/BarChart';
import * as chartConfigs from '@/components/Charts/config';
import TaskList from './Dashboard/TaskList'
import UserTable from './Dashboard/UserTable'
import config from '@/config';

export default {
  components: {
    Card,
    LineChart,
    BarChart,
    TaskList,
    UserTable
  },
  data(){
    return{
      htmls: ['丁道尔William Tyndale(1494-1536)', '克里威廉William Carey(1761-1834)', '加尔文John Calvin(1509-1564)', '卜维廉William Booth(1829-1912)', '古约翰Jonathan Goforth(1859-1936)', '叨雷R. A. Torrey(1856-1927)', '司布真C. H. Spurgeon(1834-1891)', '奥古斯丁A. Augustinus(354-430)', '威克里夫John Wycliffe(1328-1384)', '宋尚节John Song(1901-1944)', '宣信A. B. Simpson(1843-1919)', '宾路易师母J. Penn-Lewis(1832-1905)', '怀特腓G. Whitefield(1714-1769)', '慕勒George Muller(1805-1898)', '慕安得烈Andrew Murray(1828-1917)', '慕迪D. L. Moody(1837-1899)', '戴德生J. Hudson Taylor(1832-1905)', '摩根G. C. Morgan(1863-1945)', '本仁·约翰John Bunyan(1627-1688)', '查理卫斯理Charles Wesley(1707-1788)', '爱德华滋J. Edwards(1703-1758)', '王明道Ming Dao Wang(1900-1991)', '盖恩夫人Madame Guyon(1648-1717)', '约翰.牛顿John Newton(1725-1807)', '约翰·诺克斯John Knox(1505-1572)', '约翰卫斯理John Wesley(1703-1791)', '罗伯斯E. Roberts(1878-1950)', '芬妮克罗斯比F. Crosby(1820-1915)', '芬尼C. Finney(1792-1843)', '辛生道夫C. N. Zinzendorf(1700-1760)', '达秘J. N. Darby(1800-1882)', '迈尔F.B.Meyer(1847-1929)', '钟马田D. M. Lloyd-Jones(1899-1981)', '陶恕A.W. Tozer(1897-1963)', '马丁·路德Martin Luther(1483-1546)']
  }
  },
  computed:{
    enableRTL() {
      return this.$route.query.enableRTL;
    },
    isRTL(){
      return this.$rtl.isRTL;
    }
  },
  methods:{
  },
  mounted(){
    this.i18n = this.$i18n;
    if (this.enableRTL) {
      this.i18n.locale = 'ar';
      this.$rtl.enableRTL();
    }
  },
  beforeDestroy() {
    if (this.$rtl.isRTL) {
      this.i18n.locale = 'en';
      this.$rtl.disableRTL();
    }
  }
}
</script>
